<template>
  <div>
      <breadcrumb :data="['']"></breadcrumb>
    <div>
      <el-table :data="workList" width="100%" :default-sort="{prop:'end_time',order:'descending'}">
        <el-table-column type="index" width="50px"></el-table-column>
        <el-table-column prop="test_name" sortable label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="demand" label="" show-overflow-tooltip></el-table-column>
        <el-table-column label="" width="100px">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.submit_time ==0" type="danger"></el-tag>
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="score" sortable width="80px" :formatter="getScore"></el-table-column>
        <el-table-column  label="" prop="start_time" sortable width="150px" :formatter="getStartTime"></el-table-column>
        <el-table-column  label="" prop="end_time" sortable width="150px" :formatter="getEndTime"></el-table-column>
        <el-table-column label="" fixed="right" width="70px">
          <template slot-scope="scope">
            <el-button type="text" size="mini" @click="workDetail(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "LastWork",
  data() {
    return {
      workList: [],
      moment: ""
    };
  },
  created() {
    this.moment = this.$moment;
    let u_id = getClientUserInfo().u_id;
    let c_id = this.$route.params.c_id;
    $httpc
      .get(`/user/${u_id}/course/${c_id}/test?type=2&isCurrent=false`)
      .then(res => {
        this.workList = res;
      });
  },
  methods:{
    getStartTime(row){
      return this.$moment(row.start_time).format('YYYY-MM-DD HH:mm')
    },
    getEndTime(row){
      return this.$moment(row.end_time).format('YYYY-MM-DD HH:mm')
    },
    getScore(row){
      return row.score==null? '':row.score;
    },
    workDetail(row){
      this.$router.push(`last_exam/${row.test_id}?action=watch&isSubmit=${row.submit_time==0?'no':'yes'}`);
    }
  }
};
</script>

<style>

</style>



