<template>
  <div>
    <el-popover placement="top" trigger="hover">
      <div v-if="type=='person'">
        <div>
          <h5>{{data.b_name}}</h5>
        </div>
        <div style="margin-bottom:10px">{{data.get_condition}}</div>
        <div v-if="data.next_level==null"></div>
        <div v-else>{{nextData.next.b_name}}</div>
        <div>{{nextData.next.get_condition}}</div>
        <div>{{nextData.current}}</div>
      </div>
      <div v-else>
        {{data.get_condition}}
      </div>
      <div class="item" slot="reference">
        <img class="badge" :src="data.b_img">
        <span>{{data.b_name}}</span>
      </div>
    </el-popover>
  </div>
</template>

<script>
/**
 * 
 */
export default {
  name: "Badge",
  props: ["data", "type"],
  data() {
    return {
      u_id: getUserInfo().u_id,
      nextData: {},
      isLoading: true
    };
  },
  created() {
    if (this.data.next_level != null && this.type == "person") this.getData();
  },
  methods: {
    getData() {
      $http.get(`/user/${this.u_id}/badge/${this.data.next_level}?type=${this.data.b_type}`).then(res => {
        this.nextData = res;
      });
    }
  }
};
</script>

<style scoped>
h5 {
  display: inline-block;
  margin: 0;
  margin-bottom: 5px;
  text-align: center;
}
.item {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 20px;
  padding-right: 10px;
}
.item > span {
  font-size: 0.9em;
}
</style>



