<template>
  <div>
    <el-form label-width="80px">
      <el-form-item label="">
        <el-input v-model="form.u_name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.u_type" placeholder="">
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
          <!-- <el-option label="" value="2"></el-option> -->
          <el-option label="" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.code"></el-input>
      </el-form-item>
      <el-form-item label="" v-show="currentAction=='edit'">
        <el-input v-model="form.email_addr"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.password" :placeholder="currentAction!='edit'? '':''"></el-input>
      </el-form-item>
    </el-form>
    <div class="action all-item-around">
      <el-button type="warning" size="small" @click="close()"></el-button>
      <el-button type="primary" size="small" @click="submit()"></el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "EditUser",
  data() {
    return {
      form: {},
      currentAction: "edit"
    };
  },
  methods: {
    edit(user) {
      this.currentAction = "edit";
      user = JSON.parse(user);
      user.u_type = user.u_type.toString();
      user.password = "";
      user.repeatPassword = "";
      this.form = user;
    },
    add() {
      this.form = {
        u_name: "",
        u_type: "",
        code: "",
        password: "",
      };
      this.currentAction = "add";
    },
    submit() {
      let _this = this;
      if (this.currentAction == "add") {
        $http.post("/user", this.form).then(res => {
          _this.form.u_id = parseInt(res);
          _this.$message({
            message: "",
            type: "success"
          });
          _this.$emit("closeDialog", _this.form);
        });
      } else {
        $http.put("/user/change_person_info" , this.form).then(res => {
          _this.$message({
            message: "",
            type: "success"
          });
          _this.$emit("closeDialog", _this.form);
        });
      }
    },
    close(){
      this.$emit('closeDialog');
    }
  }
};
</script>
<style scoped>
.dialog {
  width: 100%;
}
.action {
  width: 300px;
  margin: 0 auto;
}
</style>




