<template>
  <div>
    <el-steps :active="currentStep" align-center style="margin:10px;">
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>
    <!--  -->
    <el-form label-width="100px" v-show="currentStep==1">
      <el-form-item label="">
        <el-input v-model="form.test_name" style="width:250px"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input type="textarea" style="width:250px" :autosize="{minRows:2,maxRows:5}" placeholder="" v-model="form.demand"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-date-picker v-model="form.start_time" style="width:250px;" format="yyyy-MM-dd HH:mm" type="datetime" placeholder=""></el-date-picker>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.work_time" style="width:250px"></el-input>
      </el-form-item>
    </el-form>
    <!--  -->
    <chose-exam-question v-show="currentStep==2" :name="form.test_name" v-on:questionChange="changeExamQuestion" ref="choseQuestion">
    </chose-exam-question>
    <!--  -->
    <chose-class v-show="currentStep==3" v-on:class-change="classChange"></chose-class>
    <!--  -->
    <div v-show="currentStep==4">
      <el-form label-width="100px">
        <el-form-item label="">{{form.test_name}}</el-form-item>
        <el-form-item label="">
          <pre style="line-height:20px">{{form.demand}}</pre>
        </el-form-item>
        <el-form-item label="">{{startTime}}</el-form-item>
        <el-form-item label="">{{form.work_time}}</el-form-item>
        <el-form-item label="">
          <template v-if="form.get_question_type=='1'">
            {{form.questionData.qg_name}}
          </template>
          <template v-else-if="form.get_question_type=='2'">
            
          </template>
        </el-form-item>
        <el-form-item label="">
          <div v-for="item in form.choseClass" :key="item.id">
            {{item.label}}
          </div>
        </el-form-item>
      </el-form>
    </div>
    <!--  -->
    <div class="all-item-hcenter">
      <el-button size="small" type="primary" @click="currentStep--" v-show="currentStep>1"></el-button>
      <el-button size="small" type="primary" @click="nextStep" v-show="currentStep<4"></el-button>
      <el-button size="small" type="primary" @click="submit" v-show="currentStep==4"></el-button>
    </div>
  </div>
</template>

<script>
import ChoseExamQuestion from "./common/ChoseExamQuestion";
import ChoseClass from "./common/ChoseClass";
export default {
  name: "AddTest",
  components: {
    ChoseExamQuestion,
    ChoseClass
  },
  data() {
    return {
      form: {
        test_name: "",
        t_type: 2,
        start_time: "",
        end_time: "",
        work_time: 120,
        get_question_type: "",
        questionData: {},
        choseClass: [],
        c_id: "",
        demand: ""
      },
      currentStep: 1
    };
  },
  computed: {
    startTime() {
      return this.$moment(this.form.start_time).format("YYYY-MM-DD hh:mm");
    }
  },
  created() {
    this.form.c_id = this.$route.params.c_id;
  },
  methods: {
    submit() {
      $http.post(`test`, this.form).then(res => {
        alertMessage("", "success");
        this.$router.push(".");
      });
    },
    classChange(data, checked, father) {
      if (checked)
        this.form.choseClass.push({
          id: data.id,
          label: father + "-" + data.label
        });
      else
        this.form.choseClass.splice(
          this.form.choseClass.findIndex(item => item.id == data.id),
          1
        );
    },
    changeExamQuestion(data) {
      if (data == null) {
        this.form.questionData = null;
      } else {
        this.form.get_question_type = data.type;
        this.form.questionData = data.data;
      }
    },
    nextStep(){
if (this.currentStep == 1) {
        if (
          this.form.test_name == "" ||
          this.form.start_time == ""
        ) {
          alertMessage("", "error");
          return;
        }
      }
      if (this.currentStep == 2) {
        if (this.form.questionData == null) {
          alertMessage("", "error");
          return;
        }
      }
      this.currentStep++;
    }

  }
};
</script>

<style>

</style>




